import { ADMIN_DETAIL } from "./actionTypes"
import { SETDATA } from "./actionTypes"

export const getAdminDetail = (userEmail="", password="") =>  {
    return {
        type:ADMIN_DETAIL,
        payload:{"userEmail":userEmail, "password":password}
    }
}
export const setName = (id,sname,usn,department,sem,collegefee=0,busfee=0,activityfee=0,esdpfee=0,alumnifee=0) =>  {
    return {
        type:SETDATA,
        payload:{"id":id,"name":sname,"usn":usn,"department":department,"sem":sem,"collegefee":collegefee,"busfee":busfee,"activityfee":activityfee,"esdpfee":esdpfee,"alumnifee":alumnifee}
    }
}

