import React, { createContext, useState, useEffect } from "react";
import axios from "axios";
import { toast } from "react-toastify";

export let AuthContextApi = createContext();

const AuthContextProvider = ({children}) => {

    const loginApiKey = process.env.REACT_APP_ADMIN_LOGIN_API_KEY;
    const refreshApiKey = process.env.REACT_APP_ADMIN_REFRESH_API_KEY;

    const [user, setUser] = useState(1);

    useEffect(() => {
        const refresh_token = localStorage.getItem('JWT_REFRESH_KEY');
        if(refresh_token!=null){
            axios({
                method: 'GET',
                url: `${refreshApiKey}/api/auth/refresh`,
                headers: {
                    "Authorization": 'Bearer '+refresh_token
                }
            })
            .then((response)=>{
                localStorage.setItem('JWT_ACCESS_KEY',response.data.accessToken);
                setUser(1);
            })
            .catch((error)=>{
                console.log(error);
                logOut();
            });
        }else{
            logOut();
        }
    }, []);

    const login = (email, password) => { 
        axios({
            method: 'POST',
            url: `${loginApiKey}/api/auth/login`,
            data: {"username":email, "password":password}
        })
        .then((response)=>{
            localStorage.setItem('JWT_REFRESH_KEY',response.data.refreshToken);
            localStorage.setItem('JWT_ACCESS_KEY',response.data.accessToken);
            setUser(1);
        })
        .catch((error)=>{
            toast.error("Something went wrong, Please check your username and password");
            setUser(null)
        })
    }

    const logOut = () => {
        localStorage.clear('JWT_REFRESH_KEY');
        setUser(null);
    }

    return <AuthContextApi.Provider value={{user, login, logOut}} >{children}</AuthContextApi.Provider>
}

export default AuthContextProvider;
