import React,{useContext} from 'react'
import { Link } from 'react-router-dom'
import { AuthContextApi } from '../contextApi/AuthContextapi'

const LinkBranches = () => {
    const context = useContext(AuthContextApi);
    const {user} = context;

    if(!user) return null;
    return (
        <div className="midBdiv">
            <Link to="/adminHome" id="linkTag">
                <p id="linkTagPara">Branches</p>
            </Link>
        </div>
    )
}

export default LinkBranches;