import React,{useState, useEffect,useContext} from 'react'
import { useDispatch} from 'react-redux'
import { actionCreators } from '../state/index'
import PostDataHOC from '../hoc/PostDataHOC'
import axios from 'axios'
import css from './update.module.css'
import { AuthContextApi } from '../contextApi/AuthContextapi';
let url='./images/updateUserIcon.ico'

const Update = (props) => {

    const apiKey = process.env.REACT_APP_API_KEY;
    const refreshApiKey = process.env.REACT_APP_ADMIN_REFRESH_API_KEY;

    const context = useContext(AuthContextApi);
    const {logOut} = context;

    const dispatch = useDispatch();

    let { crudMethod} = props;

    const [updateName, setUpdateName] = useState("");

    const [studList, setstudList] = useState({
        "activityfee": 0,
        "alumnifee": 0,
        "busfee": 0,
        "collegefee": 0,
        "department": "",
        "esdpfee": 0,
        "id": "",
        "name": "",
        "sem": "",
        "usn": ""
    });

    const [accessToken, setAccessToken] = useState("");

    let {id,name, usn , department, sem,collgefee,busfee,activityfee,esdpfee,alumnifee} = studList;

    let hname, hvalue;
    const handleChange = (e) => {
        e.preventDefault();
        hname=e.target.name;
        hvalue=e.target.value;
        setstudList({...studList, [hname]: hvalue})
    }

    useEffect(() => {
        const refresh_token = localStorage.getItem('JWT_REFRESH_KEY');
        if(refresh_token!==null){
            axios({
                method: 'GET',
                url: `${refreshApiKey}/api/auth/refresh`,
                headers: {
                    "Authorization": 'Bearer '+refresh_token
                }
            })
            .then((response)=>{
                setAccessToken(response.data.accessToken);
            })
            .catch((error)=>{
                console.log(error);
                logOut();
            });
        }

        return(()=>{
            setstudList()
        })
    }, [])

    useEffect(() => {
        let id = localStorage.getItem("ID");
        if(accessToken!==""){
            axios({
                method: 'GET',
                // url: 'http://localhost:8080/api/students',
                url:`${apiKey}/api/students/get/${id}`,
                headers: {
                    "Authorization": 'Bearer '+ accessToken
                }
            })
            .then((response)=>{
                 setstudList(response.data);
                 setUpdateName(response.data.name);
                //  console.log('GetData() :',response);
            })
            .catch((error)=>{
                console.log(error);
            });
        }
    }, [accessToken]);
    

    const saveInRedux = (e) =>{
        dispatch(actionCreators.setName(id,name,usn,department,sem,collgefee,busfee,activityfee,esdpfee,alumnifee));
    }

    const  reset = () => {
        setstudList({...studList, name:"",usn:"",department:"",sem:""})
    }
    return (
        <>
            <div className={css.container}>
                <div className={css.subContainer1}>
                    <div>
                        <h1 className={css.h1}>Update {updateName}</h1>
                    </div>
                    <div className={css.subContainer2}>
                        <div>
                            <img src={url} alt="" />
                        </div>
                        <div>
                            <form onSubmit={crudMethod}>
                                <div className={css.mainInput}>
                                    <div className={css.subInput}>
                                        <label htmlFor="name" className={css.label}>Name</label>
                                        <input type="text" id="name" name="name" value={name} onChange={handleChange} placeholder="enter student name" required className={css.input} />
                                    </div>
                                    <div  className={css.subInput}>
                                        <label htmlFor="usn" className={css.label}>Usn</label>
                                        <input type="text" id="usn" name="usn" value={usn} onChange={handleChange} placeholder="enter student usn" required className={css.input} />
                                    </div>
                                </div>
                                <div className={css.mainInput}>
                                    <div className={css.subInput}>
                                         <label htmlFor="department" className={css.label}>Department</label>
                                         <input type="text" id="department" name="department" value={department} onChange={handleChange} placeholder="enter student department" required className={css.input} />
                                    </div>
                                    <div className={css.subInput}>
                                         <label htmlFor="sem" className={css.label}>Sem</label>
                                         <input type="text" id="sem" name="sem" value={sem} onChange={handleChange} placeholder="enter student sem" required className={css.input} />
                                    </div>
                                </div>
                                <div className={css.btnDiv}>
                                    <input type="reset" onClick={reset} value="clear" className={css.btn1} />
                                    <input type="submit" onClick={saveInRedux} value="update" className={css.btn2} />
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default PostDataHOC(Update,"put","update")
