import React,{useState,useEffect} from 'react'
import Branches from './Branches'
import BranchJson from '../brList.json';
import './scss/adminHome.scss'
import swal from 'sweetalert';
import { useNavigate } from 'react-router-dom';
let url = "./images/StudentGroup.jpg";

const AdminHome = () => {

    const navigate = useNavigate();

    const [branch, setBranches] = useState(BranchJson);

    const [selectedBranch, setSelectedBranch] = useState(null);

    useEffect(() => {
        if(selectedBranch!=null){
            swal("Enter Semester:", {
                content: "input",
              })
              .then((value) => {
                  if(value<9 && value>0){
                    // swal(`Entgered branch: ${selectedBranch}`);
                    localStorage.setItem("Branch",selectedBranch);
                    localStorage.setItem("sem",value);
                    navigate(`/main/${selectedBranch}/${value}`)
                  }
                  else{
                      swal('Invalid Semester')
                  }
                
              });
              setSelectedBranch(null);
        }
    }, [selectedBranch])

    useEffect(() => {
      localStorage.removeItem("Branch");
      localStorage.removeItem("sem");
    }, [])
    

    const allStudent = () => {
        navigate("/main")
    }

    const selectBR = (br) => {
        setSelectedBranch(br);
    }

  return (
    <div className='adminHomeContainer'>
        <div>
            <div className='allStudent' onClick={allStudent}>
                <img src={url} alt="" className='img' />
                <h2 className='H1'>All Students</h2>
            </div>
            {branch.map((data, key)=> <Branches branchName={data.br} key={key} selectBR={selectBR} url={data.url} brCode={data.branch} />)}
        </div>
        <div className='imagesList'>
            <div>
                <img src="./images/admission.png" alt="" className='limg'/>
                <img src="./images/fine.png"  alt="" className='limg'/>
            </div>
            <div>
                <img src="./images/noDue.png" alt="" className='limg'/>
                <img src="./images/idCard.png" alt="" className='limg'/>
            </div>
            <div>
                <img src="./images/marksCard.png" alt="" className='limg'/>
                <img src="./images/others.png" alt="" className='limg2'/>
            </div>
        </div>
    </div>
  )
}

export default AdminHome