import React from 'react'
import './scss/branches.scss'

const Branches = ({branchName, selectBR, url, brCode}) => {
  return (
    <div>
        <div onClick={()=>{selectBR(brCode)}} className="BranchesSubContainer">
            <img src={url} alt="img" className="brImg" />
            <h2 className='branchH1'>{branchName}</h2>
        </div>
    </div>
  )
}

export default Branches