import React from 'react'
import axios from 'axios'
import {useSelector} from 'react-redux'
import {useNavigate} from 'react-router'
import { toast } from 'react-toastify'


const PostData = (Wrappedcomponent,hold,rout) => {

    const apiKey = process.env.REACT_APP_API_KEY;

    const Wrappedfunction = (props) => {

        const navigate = useNavigate();
        
        const state = useSelector(state =>({id:state.redu.id, name: state.redu.name, usn:state.redu.usn, department:state.redu.department, sem:state.redu.sem, collegefee:state.redu.collegefee, busfee:state.redu.busfee, activityfee:state.redu.activityfee, esdpfee:state.redu.esdpfee, alumnifee:state.redu.alumnifee}));
        const {id,name, usn, department, sem, collegefee, busfee, activityfee, esdpfee, alumnifee} = state;

        // console.log('----------')
        // console.log(id);
        // console.log(name);
        // console.log(usn);
        // console.log(department);
        // console.log(sem);  
        // console.log(collegefee);
        // console.log(busfee);
        // console.log(activityfee);
        // console.log(esdpfee);
        // console.log(alumnifee);
        // console.log('------------')

        const postdataOp = (e) => {
            if(rout==='create' || rout==='feedetail'){
                e.preventDefault();
            }
            var axiosType;   

                    const access_token = localStorage.getItem('JWT_ACCESS_KEY');
                    
                    switch(hold){
                        case 'post': axiosType='POST'; break;
                        case 'put': axiosType='PUT';
                                    break;
                        default: navigate('/main'); ;
                    }

                    if((name&&usn&&department&&sem&&collegefee&&busfee&&activityfee&&esdpfee&&alumnifee)!==undefined || hold==='delete'){

                            axios({
                                method: axiosType,
                                url: `${apiKey}/api/students/${id===''? '': id}`,
                                data: {
                                    "name": name,
                                    "usn": usn,
                                    "department": department,
                                    "sem": sem,
                                    "collegefee": collegefee,
                                    "busfee": busfee,
                                    "activityfee": activityfee,
                                    "esdpfee": esdpfee,
                                    "alumnifee": alumnifee
                                },
                                headers: {
                                    "Authorization" : "Bearer "+ access_token
                                }
                            })
                        .then((response)=>{
                                if(rout==='create'){
                                    toast.success('Student created successfully')
                                }
                                else if(rout==='feedetail'){
                                    toast.success('Payment successfull')
                                }
                                else{
                                    toast.success('Task successfull')
                                }
                            })
                            .catch((error)=>{
                                console.log(error);
                                toast.error('Something went wrong...try again')
                            });
                    } 
            // }
        }
        return <Wrappedcomponent {...props} crudMethod={postdataOp}/>
    }
    return Wrappedfunction;
}

export default PostData
